<template>
    <div align="center">

        <div class="auth">
            <span>Логин</span> <br/>
            <input class="login" type="text" v-model="login" @input="normalView" @keyup.enter="sendUser"><br/>
            <span>Пароль</span><br/>
            <input type="text" v-model="password" @input="normalView" @keyup.enter="sendUser"/><br/>
            <button @click="sendUser" >Войти</button>
            <br/>
            <button id="registration" @click="show">Регистрация</button>
            <div>
                <message :message="message" :color="color"/>
            </div>
        </div>

        <div class="onMain">
            <button @click="toMain">На главную</button>
        </div>

        <div id="modalWindow" class="modal">
            <div class="modal-content">
                <registration-form :close="close"/>
            </div>
        </div>
    </div>
</template>

<script>
    import Message from "@/components/message";
    import RegistrationForm from "@/components/registration-form";

    export default {
        name: "login-form",
        components: {RegistrationForm, Message},
        data() {
            return {
                login: '',
                password: '',
                token: '',
                message: '',
                color: '',
                url: ''
            }
        },
        methods: {
            sendUser() {
                let user = {login: this.login, password: this.password};
                if (this.login === "" || this.password === "") {
                    this.message = "Необходимо заполнить оба поля";
                    this.color = 'color: red';
                    this.errorView()
                } else
                    this.$http.post(this.url + '/auth/login', user).then(response => {
                            response.json().then(data => {
                                localStorage.setItem('jwt', data.token);
                                localStorage.setItem('user',data.login)
                                this.$router.push('main')

                            })
                        }, response => {
                            if (response.status === 401) {
                                this.message = 'Неверный логин или пароль';
                                this.color = 'color: red';
                                this.errorView()
                            }
                        }
                    )
            },
            show() {
                document.getElementById('modalWindow').style.display = "block";
            },
            close() {
                document.getElementById('modalWindow').style.display = "none";
            },
            toMain() {
                this.$router.push('main')
            },
            normalView(){
                let inputs = document.getElementsByTagName('input');
                inputs[0].style.border = '2px solid lightgrey';
                inputs[1].style.border = '2px solid lightgrey';
                this.message = ''
            },
            errorView(){
                let inputs = document.getElementsByTagName('input');
                inputs[0].style.border = '2px solid red';
                inputs[1].style.border = '2px solid red';
            }
        }
    }
</script>

<style scoped>

    .onMain {
        margin-top: 2%;
    }

    .auth {
        border: 2px solid lightgrey;
        width: 30%;
        border-radius: 4px;
    }

    .modal {
        display: none;
        position: fixed;
        z-index: 1;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        overflow: auto;
        background-color: rgba(0, 0, 0, 0.8);
    }

    .modal-content {
        background-color: #0c0e3e;
        margin: 10% auto;
        padding: 30px;
        border: 1px solid #888;
        width: 30%;
    }

    @media screen and (max-width: 747px){
        .auth {
            border: 2px solid lightgrey;
            width: 60%;
            border-radius: 4px;
        }

        .modal-content {
            background-color: #0c0e3e;
            margin: 50% auto;
            padding: 30px;
            border: 1px solid #888;
            width: 60%;
        }
    }

    @media (min-width: 747px ) and (max-width: 1104px ){
        .auth {
            border: 2px solid lightgrey;
            width: 40%;
            border-radius: 4px;
            margin-top: 25% ;
        }

        .modal-content {
            background-color: #0c0e3e;
            margin: 40% auto;
            padding: 30px;
            border: 1px solid #888;
            width: 40%;
        }
    }
</style>