<template>
    <tr v-if="point.r!=-1">
        <td>{{point.id}}</td>
        <td>{{point.x}}</td>
        <td>{{point.y}}</td>
        <td>{{point.r}}</td>
        <td>{{point.hit}}</td>
    </tr>
    <tr v-else>
        <td colspan="5">Неверные данные</td>
    </tr>
</template>

<script>
    export default {
        name: "table-row",
        props: ['point'],
        created() {
            if (this.point.hit) this.point.hit = 'Входит'
            else this.point.hit = 'Не входит'
        }

    }
</script>

