<template>
    <div>
        <canvas @click="sendPoint($event)" id="canvas" width="350" height="350"/>
        <p v-if="message" style="color:red">Вы ввели недопустимый радиус</p>
    </div>
</template>

<script>
    import {eventBus} from "@/main";

    var lines = new Path2D();
    lines.moveTo(175, 348);
    lines.lineTo(175, 2);
    lines.moveTo(2, 175);
    lines.lineTo(348, 175);

    var yTriangle = new Path2D();
    yTriangle.moveTo(175, 11);
    yTriangle.lineTo(168, 11);
    yTriangle.lineTo(175, 1);
    yTriangle.lineTo(182, 11);
    yTriangle.lineTo(175, 11);

    var xTriangle = new Path2D();
    xTriangle.moveTo(339, 175);
    xTriangle.lineTo(339, 168);
    xTriangle.lineTo(349, 175);
    xTriangle.lineTo(339, 182);
    xTriangle.lineTo(339, 175);

    var xLines = new Path2D();
    xLines.moveTo(144, 172);
    xLines.lineTo(144, 178);
    xLines.moveTo(113, 172);
    xLines.lineTo(113, 178);
    xLines.moveTo(82, 172);
    xLines.lineTo(82, 178);
    xLines.moveTo(51, 172);
    xLines.lineTo(51, 178);
    xLines.moveTo(20, 172);
    xLines.lineTo(20, 178);
    xLines.moveTo(206, 172);
    xLines.lineTo(206, 178);
    xLines.moveTo(237, 172);
    xLines.lineTo(237, 178);
    xLines.moveTo(268, 172);
    xLines.lineTo(268, 178);
    xLines.moveTo(299, 172);
    xLines.lineTo(299, 178);
    xLines.moveTo(330, 172);
    xLines.lineTo(330, 178);

    var yLines = new Path2D();
    yLines.moveTo(172, 206);
    yLines.lineTo(178, 206);
    yLines.moveTo(172, 237);
    yLines.lineTo(178, 237);
    yLines.moveTo(172, 268);
    yLines.lineTo(178, 268);
    yLines.moveTo(172, 299);
    yLines.lineTo(178, 299);
    yLines.moveTo(172, 330);
    yLines.lineTo(178, 330);
    yLines.moveTo(172, 144);
    yLines.lineTo(178, 144);
    yLines.moveTo(172, 113);
    yLines.lineTo(178, 113);
    yLines.moveTo(172, 82);
    yLines.lineTo(178, 82);
    yLines.moveTo(172, 51);
    yLines.lineTo(178, 51);
    yLines.moveTo(172, 20);
    yLines.lineTo(178, 20);

    function drawCoord() {
        let canvas = document.getElementById("canvas");
        // eslint-disable-next-line no-console
        let drawing = canvas.getContext("2d");

        drawing.strokeStyle = "white";
        drawing.fillStyle = "white";
        drawing.font = "12px cursive";
        //lines
        drawing.stroke(lines);

        //Y triangle
        drawing.fill(yTriangle);
        drawing.fillText("Y", 184, 14);

        //X triangle
        drawing.fill(xTriangle);
        drawing.fillText("X", 339, 165);

        //X lines
        drawing.stroke(xLines);
        drawing.fillText("1", 203, 169);

        //Y lines
        drawing.stroke(yLines);
        drawing.fillText("1", 180, 148);
    }

    function drawFigure(R) {
        let canvas = document.getElementById("canvas");
        let drawing = canvas.getContext("2d");
        drawing.strokeStyle = "#EC1947";
        drawing.fillStyle = "#EC1947";

        drawing.clearRect(0, 0, canvas.width, canvas.height);

        drawing.beginPath();
        drawing.fillRect(175 - R * 31 / 2, 175, 31 * R / 2, 31 * R);
        drawing.fill();
        drawing.moveTo(175, 175);
        drawing.lineTo(175 - R * 31 / 2, 175);
        drawing.lineTo(175, 175 - R * 31 / 2);
        drawing.lineTo(175, 175);
        drawing.fill();
        drawing.arc(175, 175, R * 31, 3 * Math.PI / 2, 0);
        drawing.fill();

        drawCoord();
    }

    function drawPoint(x, y, color) {
        let canvas;
        canvas = document.getElementById("canvas");
        let drawing = canvas.getContext("2d");
        drawing.fillStyle = color;
        drawing.beginPath();
        drawing.moveTo(x, y);
        drawing.arc(x, y, 2, -Math.PI, Math.PI);
        drawing.fill();
    }

    function clearPoints() {
        let canvas = document.getElementById("canvas");
        let drawing = canvas.getContext("2d");
        drawing.clearRect(0, 0, canvas.width, canvas.height);
        drawCoord();
        drawFigure();
    }

    function checkArea(x, y, r) {
        let result;
        if (x >= 0 && y >= 0 && Math.sqrt(x * x + y * y) <= r) result = true;
        else if (x <= 0 && y >= 0 && y <= x + r / 2) result = true;
        else if (x <= 0 && y <= 0 && x >= -r / 2 && y >= -r) result = true;
        return result;
    }

    function redrawPoints(points, r) {
        for (let point of points) {
            let x = point.x;
            let y = point.y;
            let canvasX = x * 31 + 175;
            let canvasY = -y * 31 + 175;
            if (checkArea(x, y, r)) drawPoint(canvasX, canvasY, "#32CD32")
            else drawPoint(canvasX, canvasY, "#1e51a4")
        }
    }

    export default {
        props: ['points'],
        name: "point-canvas",
        data() {
            return {
                x: '',
                y: '',
                r: '3',
                message: false,

            }
        },
        mounted() {
            drawCoord();
            drawFigure(3);
            let pointApi = this.$resource('/point{/id}', {Headers: {'Authorization': 'Bearer_' + localStorage.getItem('jwt')}})
            pointApi.get().then(result =>
                result.json().then(points => {
                        redrawPoints(points, this.r)
                    }
                ))

        },

        created() {
            eventBus.$on('drawPoint', point => {
                let canvasX = point.x * 31 + 175;
                let canvasY = -point.y * 31 + 175;
                if (point.hit)
                    drawPoint(canvasX, canvasY, "#32CD32");
                else drawPoint(canvasX, canvasY, "#1e51a4");
            })
            eventBus.$on('changeR', r => {
                if (r <= 5 && r >= 0) {
                    drawFigure(r);
                    this.r = r
                    redrawPoints(this.points, r)
                } else this.r = r

            })
            // eslint-disable-next-line no-unused-vars
            eventBus.$on('del', param => {
                clearPoints()
                drawCoord();
                if (this.r >= 0 && this.r <= 5)
                    drawFigure(this.r)
                else if (this.r > 5) drawFigure(5)
                else {
                    drawFigure(0);
                }

            })
        },
        methods: {
            sendPoint: function (event) {
                if (this.r >= 0 && this.r <= 5) {
                    this.message = false;
                    this.x = event.offsetX;
                    this.y = event.offsetY;
                    let realX = ((this.x - 175) / 31).toFixed(2);
                    let realY = (-(this.y - 175) / 31).toFixed(2);

                    let point = {x: realX, y: realY, r: this.r};
                    let pointApi = this.$resource('/point{/id}', {Headers: {'Authorization': 'Bearer_' + localStorage.getItem('jwt')}})
                    pointApi.save({}, point).then(result =>
                        result.json().then(data => {
                                this.points.push(data);
                                if (data.hit)
                                    drawPoint(this.x, this.y, "#32CD32")
                                else drawPoint(this.x, this.y, "#1e51a4")
                            }
                        )
                    )


                } else {
                    this.message = true;
                }


            }


        }
    }
</script>

<style scoped>

</style>