<template>
    <div>
        <span>X</span><br/>
        <input style="width: 30%" type="text" placeholder="{-5 ... 3}" v-model="x"/><br/>
        <span>Y</span><br/>
        <input style="width: 30%" type="text" placeholder="{-5 ... 5}" v-model="y"/><br/>
        <span>R</span><br/>
        <input style="width: 30%" type="text" placeholder="{0 ... 5}" v-model="r" @input="changeR"/> <br/>
        <button @click="check" style="margin-top: 3%">Проверить</button>
        <br/>
        <button @click="del" style="margin: 2%">Удалить точки</button>
    </div>
</template>

<script>
    import {eventBus} from "@/main";

    export default {
        props: ['points', 'clearPoints'],
        name: "point-form",
        data() {
            return {
                x: '',
                y: '',
                r: '',
                url: ''

            }
        },
        methods: {
            check() {
                let x = this.x;
                let y = this.y;
                let r = this.r;
                if (isNaN(Number(y)) || isNaN(Number(x)) || isNaN(Number(r)))
                    this.errorView()
                else if (x <= 3 && x >= -5 && y <= 5 && y >= -5 && r >= 0 && r <= 5) {
                    this.normalView()
                    let point = {x: this.x, y: this.y, r: this.r};
                    let pointApi = this.$resource(this.url + '/point{/id}', {Headers: {'Authorization': 'Bearer_' + localStorage.getItem('jwt')}})
                    pointApi.save({}, point).then(result =>
                        result.json().then(point => {
                                this.points.push(point);
                                eventBus.$emit("drawPoint",point);
                                this.x = '';
                                this.y = '';
                                this.r = '';
                            }
                        )
                    )
                } else this.errorView()

            },
            del() {
                let pointApi = this.$resource(this.url + '/point{/id}', {Headers: {'Authorization': 'Bearer_' + localStorage.getItem('jwt')}})
                pointApi.delete().then(response => {
                        if (response.ok) {
                            this.clearPoints();
                            eventBus.$emit('del',{});
                        }
                    }
                )

            },
            changeR() {
                eventBus.$emit('changeR', this.r)
            },
            normalView() {
                let inputs = document.getElementsByTagName('input');
                inputs[0].style.border = '2px solid lightgrey';
                inputs[1].style.border = '2px solid lightgrey';
                inputs[2].style.border = '2px solid lightgrey';
            },
            errorView() {
                let inputs = document.getElementsByTagName('input');
                inputs[0].style.border = '2px solid red';
                inputs[1].style.border = '2px solid red';
                inputs[2].style.border = '2px solid red';
            }


        }
    }
</script>

<style scoped>

</style>