<template>
    <div>
        <div class="form">
            <div align="right">
                <span class="close" @click="this.close">&times;</span>
            </div>
            <h2 style="font-family: cursive">Регистрация</h2>
            <span>Логин</span><br/>
            <input type="text" v-model="login" @input="normalView" @keyup.enter="sendNewUser"/><br/>
            <span>Пароль</span><br/>
            <input type="text" v-model="password" @input="normalView" @keyup.enter="sendNewUser"/><br/>
            <button @click="sendNewUser">Зарегистрироваться</button>
        </div>
        <div>
            <message :message="message" :login="login" :password="password" :color="color"/>
        </div>
    </div>
</template>

<script>
    import Message from "@/components/message";

    export default {
        props:['close'],
        name: "registration-form",
        components: {Message},
        data() {
            return {
                login: '',
                password: '',
                message: '',
                color: '',
                url: ''
            }
        },
        methods: {
            sendNewUser() {
                let user = {login: this.login, password: this.password};
                if (this.login === "" || this.password === "") {
                    this.message = "Необходимо заполнить оба поля";
                    this.color = 'color: red'
                    this.errorView()
                } else
                    this.$http.post(this.url + '/auth/register', user).then(response => {
                        // eslint-disable-next-line no-unused-vars
                            response.json().then(data => {
                                this.message = 'Вы успешно зарегистрировались';
                                this.color = "color: green"
                            })
                        }, response => {
                            if (response.status === 401) {
                                this.message = 'Пользователь с таким логином уже зарегистрирован';
                                this.color = 'color: red';
                                this.errorView()
                            }
                        }
                    )
            },
            normalView(){
                let inputs = document.getElementsByTagName('input');
                inputs[2].style.border = '2px solid lightgrey';
                inputs[3].style.border = '2px solid lightgrey';
                this.message = ''
            },
            errorView(){
                let inputs = document.getElementsByTagName('input');
                inputs[2].style.border = '2px solid red';
                inputs[3].style.border = '2px solid red';
            }
        }
    }
</script>

<style scoped>
    .form {
        text-align: center;
    }

    .close {
        color: #aaa;
        font-size: 28px;
        font-weight: bold;


    }

    .close:hover {
        cursor: pointer;
    }

</style>