<template>
    <div>
        <div class="user-inf">
            <img src="../images/user_icon.png" width="40" class="inline">
            <p class="inline login" style="vertical-align: 10px">{{login}}</p><br/>
            <button class="button" @click="logout">logout</button>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                login: localStorage.getItem('user')
            }
        },
        name: "user-inf",
        methods: {
            logout() {
                localStorage.removeItem('jwt');
                localStorage.removeItem('user')
                this.$router.push('/')

            }
        }
    }
</script>

<style scoped>
    .user-inf {
        top: 5px;
        float: right;
    }

    .inline {
        display: inline-block;
    }

    .login {
        font-size: 20px;
        margin-left: 4%;
    }

    .button {
        margin-top: 1%;
        margin-right: 10px;
    }

</style>