<template>
    <div>
        <div style="width: 100%; display:inline-block" class="content">
            <div class="inline form" align="center">
                <point-form :points="localPoints" :clear-points="clearPoints"/>
            </div>
            <div class="inline canvas" align="center">
                <point-canvas :points="localPoints"/>
            </div>
        </div>

        <div class="table" align="center">
            <point-table  :points="localPoints"/>
            <button @click="toLogin">На стартовую</button>
    </div>

    </div>
</template>

<script>
    import PointForm from "./point-form";
    import PointTable from "./point-table";
    import PointCanvas from "@/components/point-canvas";


    export default {
        components: {PointCanvas, PointTable, PointForm},
        props: ['points'],
        name: "point-list",
        data() {
            return {
                localPoints: this.points,
                url: ''
            }
        },
        created() {
            if (localStorage.getItem('jwt') == null) {
                this.$router.push('/')
            }
            let pointApi = this.$resource(this.url + '/point{/id}', {Headers: {'Authorization': 'Bearer_' + localStorage.getItem('jwt')}})
            pointApi.get().then(result =>
                result.json().then(data =>
                    data.forEach(point => this.points.push(point))
                ))
        },
        methods: {
            clearPoints() {
                this.localPoints = [];
            },
            toLogin() {
                this.$router.push('/')
            }
        }

    }
</script>

<style scoped>
    .inline {
        display: inline-block;
    }

    .form {
        width: 50%;
        height: 100%;

    }

    .canvas {
        width: 50%;
        float: right;
        height: 100%;
        top: 50%;
    }

    .table {
        width: 100%;
        margin-top: 2%
    }

    @media (min-width: 747px ) and (max-width: 1104px ){
        .form {
            width: 50%;
            height: 100%;
            margin-top: 10%;

        }

        .canvas {
            width: 50%;
            float: right;
            height: 100%;
            top: 50%;
            margin-top: 10%;
        }

        .table {
            width: 100%;
            margin-top: 5%
        }
    }

    @media screen and (max-width: 747px){
        .inline {
            display: block;
        }
        /*.content{*/
        /*    align-content: center;*/
        /*}*/
        .form {
            width: 100%;
            height: 100%;
        }
        .canvas {
            width: 100%;
            height: 100%;
            top: 50%;
            margin-top: 10%;
        }

    }
</style>